import "./share.css";
import PermMediaIcon from "@mui/icons-material/PermMedia";
// import LabelIcon from "@mui/icons-material/Label";
import CloseIcon from "@mui/icons-material/Close";
import {
  ChromeReaderModeSharp,
  Tag,
  ReadMoreOutlined,
  Style,
} from "@mui/material";

import LocationOnIcon from "@mui/icons-material/LocationOn";
import AddReactionIcon from "@mui/icons-material/AddReaction";
import { useContext, useRef, useState, useEffect } from "react";
import axios from "axios";
import { AuthContext } from "../../contex/AuthContext";

export default function Share() {
  const { user } = useContext(AuthContext);
  const PF = process.env.REACT_APP_PUBLIC_FOLDER;
  const [file, setFile] = useState();
  const [image, setImage] = useState("");
  const [currentUser, setCurrentUser] = useState({});
  const [shareText, setShareText] = useState("");

  const desc = useRef();

  ////////get user//////
  useEffect(() => {
    const fetchUser = async () => {
      const res = await axios.get(`/api/users?username=${user.username}`);

      setCurrentUser(res.data);
    };
    fetchUser();
  }, [currentUser.username]);
  /////////////////////
  if (file) {
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onloadend = () => {
      setImage(reader.result);
    };
  }

  const submitHandeler = async (e) => {
    e.preventDefault();
    if (image) {
      setShareText("please wait...");
    } else setShareText("please select any image...");
    if (image) {
      await axios.post("/api/posts/upload/", {
        userId: currentUser._id,
        desc: desc.current.value,
        img: image,
      });
      await window.location.reload();
    }
  };

  return (
    <div className="share">
      <div className="shareWrapper">
        <div className="shareTop">
          <img
            className="shareProfileImg"
            src={
              currentUser.profilePicture
                ? currentUser.profilePicture
                : "data:image/png;base64,"
            }
            alt=""
          />
          <input
            placeholder={`What's in your mind ${currentUser.username}?`}
            className="shareInput"
            ref={desc}
          />
        </div>
        <hr className="shareHr" />
        {file && (
          <div className="shareimgContainer">
            <img className="shareImg" src={image} alt="" />
            <CloseIcon
              className="shareCancelImg"
              onClick={() => setFile(null)}
            />
          </div>
        )}
        <p>{shareText}</p>
        <form className="shareBottom" onSubmit={submitHandeler}>
          <div className="shareOptions">
            <label htmlFor="file" className="shareOption">
              <PermMediaIcon htmlColor="tomato" className="shareIcon" />
              <span className="shareOptionText">Photo Or Video</span>
              <input
                style={{ display: "none" }}
                type="file"
                id="file"
                accept=".png,.jpg,.jpeg"
                onChange={(e) => setFile(e.target.files[0])}
              />
            </label>

            <div className="shareOption">
              <LocationOnIcon htmlColor="green" className="shareIcon" />
              <span className="shareOptionText">Location</span>
            </div>
            <div className="shareOption">
              <AddReactionIcon htmlColor="green" className="shareIcon" />
              <span className="shareOptionText">Feelings</span>
            </div>
          </div>
          <button type="submit" className="shareButton">
            ShareButton
          </button>
        </form>
      </div>
    </div>
  );
}
